.im-dialog:hover{
    background: #f2f6fa
}

.im-dialog{
    cursor: pointer;
}

.im-dialog-active {
    background-color: #6490b1;
    color: #fff;
    cursor: pointer;
}

.content{
    word-wrap: break-word;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.photo {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    overflow: hidden;
    float: left;
    margin-right: 10px;
}

.content-wrap{
    height: 48px;
    overflow: hidden;
}

.peer-initials{
    line-height: 48px;
    font-size: 18px;
    display: block;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.inner-div{
    padding: 10px;
}


